.show-part-container:nth-child(3) {
  margin: 2rem;
}
.show-part-container:nth-child(3)::before {
  content: "";
  display: inline-block;
  background-color: #e0b441;
  width: 100vw;
  height: 5px;
  margin: 0 -2rem;
}
.lil-card-describing-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 10rem;
}

.lil-card-descring-infos-container {
  width: 40rem;
  height: 20rem;
  background-color: #ffdd7b;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.photo-describe-informations {
  margin: 6rem;
}

.lil-card-describing-infos-content {
  font-size: 2rem;
  flex-direction: column;
  width: 20vw;
}
.photo-container {
  display: flex;
  height: 100%;
  padding: 30px;
}
.photo-container img {
  width: 24rem;
  height: 21rem;
  border-radius: 20px;
}

.more-about-driver-card {
  width: 60rem;
  height: 20rem;
  background-color: #ffdd7b;
  border-radius: 10px;
  display: flex;
  font-size: 1.5rem;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2rem;
}

.message-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 55rem;
}

.contact-content {
  display: flex;
  flex-direction: row;
  gap: 7.5rem;
}

.filter-container-part {
  gap: 2rem;
}
.infos-part-container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.input-comment-something {
  width: 40.063rem;
  height: 4rem;
  border-radius: 30px;
  border: none;
  font-size: 1.2rem;
  font-family: "Poppins";
  padding: 1.5rem 4rem;
  outline-color: var(--main-yellow);
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.363);
}

.button-send-comment {
  width: 11.375rem;
  height: 4rem;
  border-radius: 20px;
  background-color: var(--button-contract-yellow);
  border: none;
  font-size: 2rem;
}

.container-comment-part {
  display: flex;
  align-items: center;
}

.comments-part {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  height: 70vh;
  width: 60vw;
  padding: 1.5rem;
  overflow: scroll;
  overflow-x: hidden;
}
.Comment-area {
  display: flex;
  flex-direction: column;
  gap: 40px;
  padding: 20px;
}

.comments-part::-webkit-scrollbar {
  width: 7px;
  background-color: var(--main-yellow);
  border-radius: 15px;
}
.filter-container-part {
  margin-top: 2rem;
}

.container-button-contratar {
  position: relative;
  margin-bottom: 10rem;
}
.container-button-contratar button {
  position: absolute;
  top: calc(50% - 50px);
  left: 50%;
  transform: translateX(-50%);
}
.container-button-contratar::after {
  content: "";
  display: inline-block;
  background-color: #e0b441;
  width: 100vw;
  height: 5px;
  margin: 0 -2rem;
}

.Contratar-button-perfil {
  width: 20.375rem;
  height: 7rem;
  border-radius: 20px;
  background-color: var(--button-contract-yellow);
  border: none;
  font-size: 2.5rem;
}
.container-carousel-van {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-around;
}

@media (max-width: 375px) {
  .lil-card-describing-container {
    flex-direction: column;
  }
  .photo-container img {
    width: 250px;
    height: 250px;
  }
  .more-about-driver-card {
    height: 11rem;
    width: 21rem;
  }
  .message-content {
    font-size: 1rem;
  }
  .contact-content {
    flex-direction: column;
    align-items: center;
    font-size: 1rem;
    gap: 1px;
  }
  .Average-price-driver {
    font-size: 1rem;
  }
  .Contratar-button-perfil {
    width: 15rem;
    height: 5rem;
    font-size: 1rem;
  }

  .container-button-contratar button {
    top: calc(50% - 40px);
  }

  .lil-card-describing-infos-content {
    width: 0;
    font-size: 1rem;
  }
}

.container-avaliacao {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-evenly;
  padding: 40px;
  height: auto;
}

.star {
  font-size: 50px;
}

.container-avaliacao-motorista {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 38px;
}
